<template>
  <v-card raised>
    <v-card-title>Soal</v-card-title>
    <v-list-item>
      <v-container class="pt-0">
        <v-row class="px-2">
          <v-col v-for="(i, index) in gridContents" :key="i.id" cols="3" class="pa-2">
            <v-btn
              :outlined="!(i.id === active)"
              :color="i.id === active ? '#00563D' : ''"
              :class="[
                i.filled ? 'filled' : 'normal',
                'pa-0',
                i.id === active ? 'white--text' : ''
              ]"
              style="min-width: 0;"
              block
              @click="changeActive(i.id)"
            >{{ index + 1 }}</v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-list-item>
    <v-card-actions>
      <v-btn color="#00563D" block class="mx-auto white--text">Selesai</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  props: {
    gridContents: {
      type: Array,
      required: true
    },
    active: {
      type: Number,
      required: true
    }
  },
  methods: {
    changeActive(id) {
      this.$emit("changeActive", id);
    }
  }
};
</script>

<style lang="scss" scoped>
.normal {
  border: 2px solid rgba(0, 0, 0, 0.5);
}

.filled {
  border: 2px solid #3ea48f;
  background: rgba(62, 164, 143, 0.3);
}
</style>
