                  <v-col cols="12">
                    <component
                      :is="forward ? 'v-scroll-x-transition' : 'v-scroll-x-reverse-transition'"
                      leave-absolute
                    >
                      <v-card v-for="(item) in props.items" :key="item.id" raised>
                        <v-container>
                          <v-row>
                            <v-col cols="12">
                              <h1>{{ item.id }}</h1>
                              <p>{{ item.description }}</p>
                            </v-col>
                          </v-row>
                          <v-row dense v-for="option in item.options" :key="option">
                            <v-col cols="12">
