              <template v-slot:header>
                <v-data-footer
                  :pagination.sync="pagination"
                  :options.sync="options"
                  disable-items-per-page
                >
                  <template
                    v-slot:page-text="pagetext"
                  >{{ pagetext.pageStart }} of {{ pagetext.itemsLength }}</template>
                </v-data-footer>
