      forward: true
    };
  },
  computed: {
    activeProblem() {
      return this.gridContents.find(item => item.id === this.active);
    },
    active() {
      return this.pagination.page;
    }
  },
  watch: {
    pagination: function(newOption, oldOption) {
      if (oldOption.page <= newOption.page) {
        this.forward = true;
      } else {
        this.forward = false;
      }
    }
  },
  methods: {
    updatePagination(e) {
      this.pagination = e;
    },
    changeActive(id) {
      // If we're changing directly, somehow the direction doesn't update fast enough
      this.forward = !!(this.pagination.page <= id);
      this.$nextTick().then(() => {
        this.pagination.page = id;
        this.options.page = id;
      });
    }
  }
};
</script>

